body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-wrapper {
  margin: 0 auto;
  display: grid;
  width: 1200px;
  grid-template-areas:
  "h h"
  "n c";

   grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    /* grid-gap: 10px; */
}


.app-wrapper-content {
  grid-area: c;
  background-color: #9388f5;
}
.Navbar_nav__3gSkF {
    grid-area: n;
    background-color: #2f2fa2;
    padding: 20px;
}

.Navbar_item__2ufRQ a {
    color: white;
    text-decoration: none;
}

.Navbar_item__2ufRQ a.Navbar_activeLink__25a8D {
    color: gold;
}



.Header_header__1xnBv {
    grid-area: h;
    background-color: #4cf6f6;
}

.Header_header__1xnBv img {
    width: 50px;
    height: 20px;
}

.Header_loginBlock__2XFe3 {
    float: right;
}

.Header_loginBlock__2XFe3 button{
